@import url('https://fonts.googleapis.com/css2?family=Passion+One&display=swap');

$font-1: 'Passion One', sans-serif;

$color-1: rgba(255, 255, 255, 1);
$color-2: rgba(88, 29, 145, 1);

$white: rgba(245, 250, 255, 1);
$black: rgba(8, 8, 40, 1);

$borderRadius: 25px;
//$shadow: 0 1px 3px #333;


*{
    margin: 0;
    padding: 0;
}

body {
    font-family: $font-1;
}

button {
    cursor: pointer;
}

li {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: $white;
    cursor: pointer;
}

a:visited {
    color: $white;
}

