.Navigation {
    height: 50px;
    width: 100%;
    position: absolute;
    top: 0%;
    margin: auto; //parent doit etre display flex pour pouvoir le centrer
    /*display: grid;
    grid-template-columns: 4fr;*/
}

ul {
    display: flex;
    height: 100%;
    width: 800px;
    margin: auto;
    
}

li {
    margin: 25px;
    flex: auto;
    background: rgb(18,18,205);
    background: linear-gradient(180deg, rgba(18,18,205,1) 0%, rgba(105,11,142,1) 92%);
    border-radius: $borderRadius;
    list-style-position:inside;
    border: 1px solid rgb(206, 243, 250);
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

span {
   font-size: 20px;
    color: $white;
}