.StudyText {
    margin: 0;
    display: grid;
    min-height: 100%;
    grid-template-columns: 100px 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        "a b c c"
        "d e f f";
        color: $white;
        
}

.bloc:nth-child(1) {
    grid-area: a;
    font-size: 25px;
    margin: auto;
    margin-top: 50%;
    margin-right: 0;
    color: rgba(97, 128, 196, 0.466);
}

.bloc:nth-child(2) {
    grid-area: b;
    font-size: 9vw;
    text-align: left;
    margin: auto;
    margin-left: 0;
    //color: linear-gradient(180deg, rgba(18,18,205,1) 0%, rgba(105,11,142,1) 92%);
    color: rgba(0, 146, 255, 0.35);
    background: -webkit-linear-gradient(rgba(146, 83, 228, 0.61), rgba(80, 159, 190, 0.61));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.bloc:nth-child(3) {
    grid-area: c;
    font-size: 2rem;
    margin: auto;
    //text-align: center;
    //margin-top: 2rem;
    margin-left: 0;
}

.bloc:nth-child(4) {
    grid-area: d;
    font-size: 25px;
    margin: auto;
    margin-top: 50%;
    margin-right: 0;
    color: rgba(97, 128, 196, 0.466);
    
}

.bloc:nth-child(5) {
    grid-area: e;
    font-size: 6vw;
    margin: auto;
    text-align: center;
    margin-left: 0;
    color: rgba(0, 146, 255, 0.35);
    background: -webkit-linear-gradient(rgba(146, 83, 228, 0.61), rgba(80, 159, 190, 0.61));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    
}

.bloc:nth-child(6) {
    grid-area: f;
    font-size: 2rem;
    //text-align: center;
    //margin-top: 2rem;
    margin: auto;
    margin-left: 0;
}



